import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DemoDataService } from 'src/app/services/demo-data/demo-data.service';
import { DomSanitizer } from '@angular/platform-browser';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import {
    faSpotify,
    faYoutube,
    faSoundcloud,
} from '@fortawesome/free-brands-svg-icons';

@Component({
    selector: 'app-track',
    templateUrl: './track.component.html',
    styleUrls: ['./track.component.scss'],
})
export class TrackComponent implements OnInit {
    data: any;
    trackData: any;
    embedVideoUrl: any;
    numLinks: number;

    // Icons
    faArrowRight = faArrowRight;
    faSpotify = faSpotify;
    faYoutube = faYoutube;
    faSoundcloud = faSoundcloud;

    innerWidth: number;

    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private demoDataService: DemoDataService,
        private sanitizer: DomSanitizer
    ) {}

    ngOnInit(): void {
        this.data = this.demoDataService.getData();
        this.route.params.subscribe((val) => {
            this.setTrack(val.track);
        });
    }

    setTrack(trackName: string) {
        switch (trackName) {
            case 'happydays':
                this.setEmbedVideoUrl(1);
                // this.setMeta();
                break;
            case 'igtba':
                this.setEmbedVideoUrl(2);
                // this.setMeta();
                break;
            case 'drive':
                this.setEmbedVideoUrl(3);
                // this.setMeta();
                break;
            case 'dissonance':
                this.setEmbedVideoUrl(4);
                // this.setMeta();
                break;
            default:
                this.router.navigate(['my-music']).then();
                break;
        }
    }

    setEmbedVideoUrl(index: number) {
        // * Set trackData
        // * Currently using local data
        this.trackData = this.data[index];
        const temp =
            'https://www.youtube.com/embed/' +
            this.extractYoutubeVideoId(this.trackData.links.youtube);
        this.embedVideoUrl =
            this.sanitizer.bypassSecurityTrustResourceUrl(temp);
        this.numLinks = Object.keys(this.trackData.links).length;
    }

    extractYoutubeVideoId(link: string): string {
        let video_id = link.split('v=')[1];
        const ampersandPosition = video_id.indexOf('&');
        if (ampersandPosition !== -1) {
            video_id = video_id.substring(0, ampersandPosition);
        }

        return video_id;
    }
}
